  position: absolute;
  left: 50%;
  top: 54%;

  transform: translate(-50%, -50%);
  @include commonCardStyles(100%, 413px, 540px, $backgroundColorCard);

  .logo{
    margin-bottom: 32px;

    font-weight: 600;
    font-size: 26px;
    line-height: 39px;


  }
  .title{
    margin-bottom: 40px;

    font-weight: 600;
    font-size: 22px;
    line-height: 33px;
  }
  .emailBlock{
    margin-bottom: 30px;

    text-align: left;

    & span{
      font-size: 13px;
      line-height: 20px;

      opacity: 0.7;
    }


    .inputText{
      width: 320px;

      margin: 0;

      padding: 10px 0 5px 0;

      background-color: inherit;

      border-top:0;
      border-left: 0;
      border-right: 0;
      border-bottom: 2px solid #24254A;

      box-shadow: none;

      color:black;

      font-size: 16px;
    }
  }
