  .inputItem {
    display: flex;
    flex-direction: column;
    margin-top: 24px;
    border: none;
    border-bottom: #F9F9FE;
    font-size: small;


    position: relative;



    label {
      text-align: left;
      color: #C0C0C0;
    }

    input {
      margin-top: 7px;

      background-color: white;

      &::placeholder {
        color: #9890c7;
      }
    }

    input[type="text"],
    input[type="password"] {
      @include commonInputStyles();
    }
  }
  .link{
    margin-bottom: 30px;

    display: block;

    font-size: 14px;
    line-height: 17px;

    transform: translateX(27%);

    color:black;

    text-decoration: none;

    transition: all .2s ease-in-out;

    &:active{
      color:goldenrod;
    }
  }
  .checkboxBlock{
    margin-bottom: 30px;
    text-align: left;

    & input {
      margin-right: 10px;
    }


  }

  .loginBtn{
    margin-bottom: 20px;
    width: 266px;
    height: 36px;

    background-color: #9890c7;

    border-radius: 20px;

    color:white;
    font-size: 14px;

    border-style: none;

    transition: all .2s ease-in-out;

    &:active{
      background-color: goldenrod;
    }
  }
  .redirectBlock{
    margin-top: 20px;

    display: flex;
    flex-direction: column;
    gap: 10px;
    & span{
      font-size: 13px;
      line-height: 20px;

      opacity: 0.7;
    }
    .signBtn{


      background-color: inherit;
      border-style: none;

      transition: all .2s ease-in-out;

      &:active{
        color:goldenrod;
      }
    }
  }
}